@import './nav/index_out';
@import './footer/index';

html, body {
  color: #fff;
  height: 100%;
  width: 100%;
  line-height: 2rem;
  -webkit-align-items: center;  
  -ms-flex-align: center;  
  align-items: center;
  font-family: Helvetica, "Microsoft Jhenghei", Arial, "Raleway", Geneva, sans-serif;
  h1, h2, h3, h4, h5, h6, p {
    font-family: Helvetica, "Microsoft Jhenghei", Arial, "Raleway", Geneva, sans-serif;
  }
  a {
    color: #2EBAFF;
    text-decoration: none !important; 
    &:hover {
      text-decoration: none; 
      opacity: 0.8;
    }
  }
}

.header {
  padding: 120px 50px;
  width: 100%;
  height: 93%;
  margin-top: -20px;
  background-size: cover;
  background-position: center center;

  h1 {
    font-weight: bold;
    font-size: 4rem;
  }
}

//  background: url('https://c1.staticflickr.com/3/2859/9387437445_b44302b59d_b.jpg');

#background {
  position: fixed;
  top: 0;
  z-index: -2;
  width: 100%;
  height: 100%;
  color: #fff;
  background: url(https://c2.staticflickr.com/8/7214/13717611224_a31fb184ff_b.jpg),#fff;
  background-size: cover!important;
  background-position: center center!important;
  -webkit-animation: fadein .5s ease 0s forwards;
}

.background-cover {
  /*background-color: rgba(217, 238, 255, 0.79);*/
  background-color: rgba(0, 0, 0, 0.2);
  height: 100vh;
  top: 0;
  width: 100vw;
  position: fixed;
  z-index: -1;
}
.right-block {
  float: right;
  text-align: center;
}

h3 {
  line-height: 2.4rem;
  font-size: 2rem;
  a {
    color: #E2E12D;
    &:hover {
      color: #E2E12D;
    }
  }
}


.signin_btn {
  padding: 1.2rem 1.6rem;
  border-radius: 12px;
  border: none;
}

.note {
  font-size: 1rem;
}


@media all and (max-width: 700px) {
  .container-fluid>.navbar-collapse, .container-fluid>.navbar-header, .container>.navbar-collapse, 
  .container>.navbar-header {
    margin: 0px;
    float: left;
  }
  .nav>li {
    width: 70px;
    float: right;
    margin-right: 10px;
    text-align: center;
  }
  .navbar-nav {
    margin: 0;
    float: right;
    width: 150px;
  }
  .container {
    h1 {
      font-size: 2.4rem;
    }
    h3 {
      font-size: 1.4rem;
      line-height: normal;
    }
  }
  .right-block {
    margin-top: 10px;
    position: absolute
  }
}
